<template>
  <div>
    <take-part-modal
      v-if="take_part"
      @toggle_take_part="toggleTakePart"
    ></take-part-modal>
    <div v-if="tournamentInfo.data" class="container">
      <h1 class="title">
        {{ tournamentInfo.data.title.toUpperCase() }}
      </h1>
      <div class="bold start">
         :
        <p class="orange">{{ dateTime.substring(0, dateTime.length - 3) }}</p>
      </div>

      <div class="text">
        <div class="imgContainer">
          <img :src="tournamentInfo.data.image" alt="" />
        </div>
        <button v-if="!registered" class="button" @click="toggleTakePart">
          
        </button>
        <button v-else class="button">
           ID   lobby
        </button>
        <!-- <ul class="nav nav-tabs m-3">
        <li class="nav-item">
          <a class="nav-link" aria-current="page" href="#"></a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#"></a>
        </li>
      </ul> -->
        <ul class="tab_ul">
          <li ref="info" @click="setComponent('info')" class="active"></li>
          <li ref="gamers" @click="setComponent('gamers')" class="tab">
            
          </li>
        </ul>
        <div v-if="component === 'info'" id="info">
          <div class="mode">
            <div>: {{ tournamentInfo.data.type }}</div>
            <div>: {{ tournamentInfo.data.map }}</div>
          </div>
          <div class="description">
            <p class="header">   :</p>
            <p>
              1.    "",   nickname  
                 (nickhame      
                 ).     - 5$.
            </p>
            <p>
              2.  10          
                 ,     .
                        ,
                 " ID   lobby".
            </p>
            <p>3.       3$   kill.</p>
            <p>
              4.  kill-      
               .
            </p>
            <p>5.      1 .</p>
          </div>
        </div>
        <div v-else>
          <div v-for="name in users_data" :key="name._id">
            {{ `${name.nickName}` }}
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="spinner-border" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
    </div>
  </div>
</template>

<script>
import AxiosApi from "../../AxiosApi";
import TakePartModal from "../ui/TakePartModal.vue";
export default {
  components: { TakePartModal },
  data() {
    return {
      tournamentInfo: { data: null },
      registered: false,
      take_part: false,
      component: "info",
      users_data: "",
    };
  },
  methods: {
    getUsersData() {
      const token = localStorage.getItem("token");
      var myHeaders = new Headers();
      myHeaders.append("Authorization", `Bearer ${token}`);

      var requestOptions = {
        method: "GET",
        headers: myHeaders,
      };
      const url = `/api/v1/tournaments/${this.$route.params.id}/register`;
      const data = fetch(url, requestOptions)
        .then((response) => response.json())
        .then((result) => (this.users_data = result.data))
        .catch((error) => console.log("error", error));
    },
    toggleTakePart() {
      this.take_part = !this.take_part;
    },
    setComponent(name) {
      this.component = name;
      if (name === "info") {
        this.$refs.info.classList.add("active");
        this.$refs.info.classList.remove("tab");
        this.$refs.gamers.classList.remove("active");
        this.$refs.gamers.classList.add("tab");
      } else {
        this.$refs.gamers.classList.add("active");
        this.$refs.gamers.classList.remove("tab");
        this.$refs.info.classList.remove("active");
        this.$refs.info.classList.add("tab");
      }
    },
  },
  computed: {
    dateTime() {
      const date = new Date(this.tournamentInfo.data.startsAt);
      // const dateT = new Date(date);
      // return datetime;
      const date_time = date.toLocaleString();

      return `${date_time}`;
    },
  },
  async created() {
    this.getUsersData();

    try {
      this.tournamentInfo = await AxiosApi.getTournament(this.$route.params.id);
    } catch (err) {
      this.error = err.message;
    }
  },
};
</script>

<style scoped>
.container {
  margin-top: 70px;
}
.imgContainer {
  max-width: 650px;
  min-width: 350px;
  height: 350px;
  display: block;
  margin-bottom: none;
}
img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.text {
  color: #77681d;
  font-weight: bold;
}
.button {
  background-color: #f2a900;
  border: #f2a900;
  font-weight: bold;
  padding: 10px;
  color: #fff;
  margin-top: 30px;
  width: 200px;
}
.start {
  font-size: 1.4em;
  font-weight: bold;
}
.description {
  margin-top: 20px;
  font-size: 17px;
  max-width: 650px;
}
.mode {
  margin-top: 20px;
  font-size: 18px;
}
p.orange {
  color: #de8d00;
  display: inline;
}
p.header {
  color: #de8d00;
  display: block;
  font-size: 20px;
  text-transform: uppercase;
}
.tab {
  list-style-type: none;
  display: inline;
  text-transform: uppercase;
  padding: 10px 30px;
  border: 2px solid #090907;
  color: #090907;
}
.tab:hover {
  cursor: pointer;
  border-color: #ebb014;
  color: #ebb014;
}
.tab:first-of-type {
  margin-left: 0px;
}
.active:hover {
  background-color: #77681d;
  cursor: pointer;
  border-color: #77681d;
}
.tab_ul {
  margin-top: 40px;
  max-width: 650px;
  padding: 0px;
}
.active {
  background-color: #090907;
  color: white;
  list-style-type: none;
  display: inline;
  text-transform: uppercase;
  padding: 10px 30px;
  border: 2px solid #090907;
}
</style>
